.skills {
    margin-top: 1rem;
}

.skills ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.skills li {
    background-color: #f1f1f1;
    color: #333;
    padding: 0.3rem 0.6rem;
    border-radius: 5px;
    font-size: 0.9rem;
}